<template>
    <view class="order_pay">
        <!-- tabbar -->
        <cu-custom><block slot="content">订单支付</block></cu-custom>
        <!-- 订单信息 -->
        <view class="order_info">
            <view class="flex justify-start align-start order_info_single">
                <view class="order_info_single_label">订单编号：</view>
                <view class="order_info_signle_value">{{orderInfo.orderNo}}</view>
            </view>
            <view class="flex justify-start align-start order_info_single">
                <view class="order_info_single_label">货物名称：</view>
                <view class="order_info_signle_value">{{orderInfo.goodsName}}</view>
            </view>
            <view class="flex justify-start align-center order_info_double">
                <view class="order_info_double_left">重量：{{orderInfo.weight}}吨</view>
                <view class="order_info_double_right">损耗范围：{{orderInfo.lossRange}}</view>
            </view>
            <view class="flex justify-start align-start order_info_single">
                <view class="order_info_single_label">装车地点：</view>
                <view class="order_info_signle_value">{{orderInfo.loadingAddress}}</view>
            </view>
            <view class="flex justify-start align-start order_info_single">
                <view class="order_info_single_label">卸车地点：</view>
                <view class="order_info_signle_value">{{orderInfo.unloadingAddress}}</view>
            </view>
            <view class="real_pay">应付：￥{{orderInfo.totalMoney}}</view>
        </view>
        <!-- 支付选择选项 -->
        <view class="things_width_full order_pay_type">
            <!-- <view class="flex justify-between align-center order_pay_type_item">
                <view class="flex justify-start align-center order_pay_type_item_left">
                    <image class="pay_icon" src="./../../../../../static/images/global/pay_icon1.png" mode="widthFix" />
                    <view class="pay_name">余额支付<text class="balance">(总余额：123元)</text></view>
                </view>
                <view class="order_pay_type_item_right">
                    <image class="select_icon" src="./../../../../../static/images/global/select0.png" mode="widthFix" />
                </view>
            </view>
            <view class="flex justify-between align-center order_pay_type_item">
                <view class="flex justify-start align-center order_pay_type_item_left">
                    <image class="pay_icon" src="./../../../../../static/images/global/pay_icon2.png" mode="widthFix" />
                    <view class="pay_name">微信支付</view>
                </view>
                <view class="order_pay_type_item_right">
                    <image class="select_icon" src="./../../../../../static/images/global/select1.png" mode="widthFix" />
                </view>
            </view> -->
            <view class="flex justify-between align-center order_pay_type_item">
                <view class="flex justify-start align-center order_pay_type_item_left">
                    <image class="pay_icon" src="./../../../../../../static/images/global/pay_icon3.png" mode="widthFix" />
                    <view class="pay_name">银行卡支付</view>
                </view>
                <view class="order_pay_type_item_right">
                    <image class="select_icon" src="./../../../../../../static/images/global/select1.png" mode="widthFix" />
                </view>
            </view>
        </view>
        <!-- 银行支付，1.0.0只提供上传收据验证 -->
        <view class="upload_box bg-white">
            <view class="cu-bar">
                <view class="action title">
                    上传收据
                </view>
            </view>
            <view class="cu-form-group">
                <view class="grid col-4 grid-square flex-sub">
                    <view class="bg-img" v-for="(item,index) in imgList" :key="index" @tap="ViewImage" :data-url="imgList[index]">
                    <image :src="imgList[index]" mode="aspectFill"></image>
                        <view class="cu-tag bg-red" @tap.stop="DelImg" :data-index="index">
                            <text class='cuIcon-close'></text>
                        </view>
                    </view>
                    <view class="solids" @tap="ChooseImage" v-if="imgList.length<1">
                        <text class='cuIcon-cameraadd'></text>
                    </view>
                </view>
            </view>
        </view>
        <!-- 支付按钮 -->
        <view class="flex justify-center align-center btn_box">
            <button class="cu-btn pay_btn" @tap="formSubmit" :disabled="buttonType">支付</button>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>

<script>
    import { getGoodsOrderDetail_api,upload_image_api,payGoodsOrder_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
	let that = null;
export default {
    data() {
        return {
            appBaseData:null,//全局配置信息
            userInfo: {},
            order_id: '',
            orderInfo: null,// 订单详情
            imgList: [],// 收据
            pay_pic_id: '',// 收据id
            buttonType: false,
        }
    },
    watch: {
        '$store.getters.base_buttonType': function(val) {
            that.buttonType = val;
        },
    },
    methods: {
        ...mapMutations([
            'modal',
            'base'
        ]),
        /**选择图片
         * @description: 
         * @param {*}
         * @return {*}
         */
        ChooseImage() {
            uni.chooseImage({
                count: 4, //默认9
                sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
                sourceType: ['album'], //从相册选择
                success: (res) => {
                    if (this.imgList.length != 0) {
                        this.imgList = this.imgList.concat(res.tempFilePaths)
                    } else {
                        this.imgList = res.tempFilePaths
                    }
                    let params = res.tempFilePaths[0];
                    upload_image_api(params,that).then(res=>{
                        that.pay_pic_id = res.data.file_id;
                    }).catch(err=>{
                        console.log(err);
                    })
                }
            });
        },
        /**图片预览
         * @description: 
         * @param {*} e
         * @return {*}
         */
        ViewImage(e) {
            uni.previewImage({
                urls: this.imgList,
                current: e.currentTarget.dataset.url
            });
        },
        /**删除图片
         * @description: 
         * @param {*} e
         * @return {*}
         */
        DelImg(e) {
            uni.showModal({
                title: '提示',
                content: '确定要删除图片吗？',
                cancelText: '取消',
                confirmText: '确定',
                success: res => {
                    if (res.confirm) {
                        that.imgList.splice(e.currentTarget.dataset.index, 1)
                    }
                }
            })
        },
        /**
         * @description: 提交表单
         * @param {*} e
         * @return {*}
         * @author: gitxinghui
         */
        formSubmit(e) {
             //定义表单规则
             that.buttonType = true;
            let rule = [
                {name:"pay_pic_id", checkType : "notnull", checkRule:"",  errorMsg:"请上传支付收据图片"},
            ];
            //进行表单检查
            let formData = {};
            formData.release_order_id = that.order_id;
            formData.pay_pic_id = that.pay_pic_id;
            let checkRes = checkForm.check(formData, rule);
            if(!checkRes){
                that.modal({name:'popTipModal',value:{type: true,tipTitle:'表单错误',tipContent:checkForm.error}});
                that.buttonType = false;
                return false;
            }
            that.modal({name:'popLoading',value:true});
            that.buttonType = true;
            payGoodsOrder_api(formData,that).then(res=>{
                that.modal({name:'popLoading',value:false});
                that.base({name:'buttonType',value:false});
                that.base({name:'refreshPage',value:true});
                that.modal({name:'popLinkDialog',value: {type:true,tipTitle:'提示',tipContent:'支付成功，等待审核',linkUrl:1}});
                that.buttonType = false;
            }).catch(err=>{
                console.log(err);
                that.modal({name:'popLoading',value:false});
                that.buttonType = false;
            })
        },
        /**我的需求货单详情
         * @description: 
         * @param {*} e
         * @return {*}
         */
        getFreightOrderInfo(e) {
            let params = {
                order_id: that.order_id
            };
            getGoodsOrderDetail_api(params,that).then(res=>{
                that.orderInfo = res;
            }).catch(err=>{
                console.log(err)
            })
        }
    },
    // 页面生命周期
    onLoad: function (options) {
        that = this;
        if (!options.order_id || options.order_id == ''|| options.order_id == 'undefined') {
            that.modal({name:'popLinkDialog',value:{type:true,tipTitle:'提示',tipContent:'该订单不存在',linkUrl:1}});
            return false;
        }
        that.order_id = options.order_id;
        that.getFreightOrderInfo();
    }
}
</script>


<style lang="scss" scoped>
    @import "./index";
</style>